@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;500;600&family=Lobster&family=Nunito+Sans:wght@300;400;800&display=swap');


/* ======= Navbar styling start ======= */
.web__navbar {
  padding: 2rem 4rem;
  display: flex;
  width: 100%;
  position: fixed;
  justify-content: space-between;
  align-items: center;
  background: rgb(0, 0, 0);
  z-index: 20;
}
.web__navbar-logo a {
  text-decoration: none;
  color: white;
  font-family: 'Lobster', cursive;
}
.web__navbar-links {
  display: flex;
  gap: 50px;
  word-spacing: 2px;
  font-family: var(--body-font);
  display: flex;
  align-items: center;
}
.web__navbar-links a {
  text-decoration: none;
  color: white;
  transition: 0.3s;
}
.web__navbar-links button {
  width: 80px;
  height: 30px;
  border: none;
  border-radius: 5px;
  transition: 0.3s;
}
.nav__responsive {
  margin-left: 1rem;
  display: none;
  position: relative;
  transition: .3s;
}
.nav__responsive svg {
  cursor: pointer;
}

.web__navbar-menu_container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  z-index: 10;
  background: rgba(255, 255, 255, 0.997);
  padding: 2rem 0;
  text-align: start;
  position: absolute;
  top: 40px;
  right: 0;
  min-width: 210px;
  border-radius: 5px;
  box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
  line-height: 30px;
}
.web__navbar-links__container a {
  text-decoration: none;
  font-size: 20px;
  color: black;
  transition: 0.3s;
}

.nav__login button {
  width: 100px;
  height: 40px;
  border: none;
  border-radius: 5px;
  transition: 0.3s;
  border: 1px solid white;
  background: black;
  color: white;
  position: relative;
  left: -35px;
  top: 10px;
}
.web__navbar-links button:hover {
  border: 1px solid white;
  background: black;
  color: white;
}


/* ======= Navar Styling End ======= */


/* ======= Header Styling Start ======= */
.web_header {
  padding: 10rem 2rem 4rem 4rem;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  gap: 20px;
}
.header-social button {
  width: 130px;
  height: 40px;
  border: none;
  border-radius: 5px;
  text-align: center;
}
.wa {
  background: #128c7e;
}
.fb {
  background: #4267b2;
}

/* ======= Header Styling End ======= */



/* ======= Home Styling Start ======= */

.home__carousel {
  padding: 6rem 4rem;
}
.carousel_img {
  height: 500px;
}

.home__drop {
  padding: 0 4rem;
}
.home__drop button {
  width: 100%;
  height: auto;
  background: rgba(245, 245, 245, 0.883);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  color: black;
  font-size: 16px;
  font-weight: 500;
}
.drop__card {
  padding: 1rem 2rem;
  border: 2px solid white;
  border-radius: 5px;
}
.drop__card-content {
  padding: 1rem 1rem;
  gap: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}
.drop__content {
  width: 120px;
  height: 60px;
  background: white;
  color: black;
  font-size: 18px;
  font-weight: 500;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  border: none;
  border-radius: 5px;
}

.carousel-desc{
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0, 0.4); /* Black w/opacity/see-through */
  color: white;
  font-weight: bold;
  border: 1px solid #f1f1f1;
  position: absolute;
  top: 20%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  width: 80%;
  padding: 20px;
  text-align: center;
}

.home__bar {
  padding: 2rem 4rem;
}
.home__bar ul {
  border: 2px solid white;
  border-radius: 5px;
  height: 120px;
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  text-align: center;
}
.home__bar ul > li {
  display: flex;
  flex-direction: column;
  font-size: 18px;
  color: white;
}
.home__bar li > span {
  font-size: 45px;
  cursor: pointer;
}
.home__bar li > a {
  text-decoration: none;
  color: white;
}

.home__artikel {
  padding: 2rem 4rem;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}
.title {
  background: white;
  color: black;
  width: 100%;
  border: none;
  border-radius: 5px;
  font-size: 26px;
}
.artikel__content {
  text-align: start;
  padding: 2rem 0;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
}
.content__card {
  border: 1px solid white;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 15px 15px;
  width: 400px;
  height: 500px;
}
.content__card img {
  width: 300px;
}
.card-title,
.card-desc {
  border-radius: 5px;
  width: 300px;
  padding: 15px 10px;
}
.card-btn button {
  width: 200px;
  height: 40px;
  border: none;
  border-radius: 10px;
  font-size: 18px;
  font-weight: 500;
  transition: 0.3s;
}
.card-btn button:hover {
  background: rgba(255, 255, 255, 0.685);
}

/* ======= Home Styling End ======= */

/* ======= Footer Styling Start ======= */


.footer {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: white;
  color: black;
}
.footer p {
  display: flex;
  justify-content: center;
  align-items: center;
  color: black;
}

/* ======= Footer Styling End ======= */



/* ======= Pemerintahan Styling Start ======= */

.desa__pem {
  display: flex;
  flex-direction: column;
  height: auto;
  justify-content: center;
  color: white;
  padding: 6rem 4rem;
}

.desa__pem-feature {
  width: 500px;
  padding-bottom: 2rem;
}

.desa__pem-feature {
  margin: 0;
}
.desa__pem-feature {
  max-width: 700px;
}

.bloc-tabs {
  display: flex;
}
.tabs {
  padding: 15px;
  text-align: center;
  width: 50%;
  background: transparent;
  color: white;
  cursor: pointer;
  border-bottom: 1px solid rgba(0, 0, 0, 0.274);
  box-sizing: content-box;
  position: relative;
  outline: none;
}
.tabs:not(:last-child) {
  border-right: 1px solid rgba(0, 0, 0, 0.274);
}

.active-tabs {
  border-bottom: 1px solid transparent;
}

.active-tabs::before {
  content: "";
  display: block;
  position: absolute;
  top: -5px;
  left: 50%;
  transform: translateX(-50%);
  width: calc(100% + 2px);
  height: 5px;
}

button {
  border: none;
}
.content-tabs {
  flex-grow: 1;
}
.content {
  background: transparent;
  padding: 20px;
  width: 100%;
  height: 100%;
  display: none;
  line-height: 20px;
  text-align: start;
}
h1 {
  padding: 0px 0 15px 0px;
  line-height: 30px;
}
hr {
  width: 100px;
  height: 1px;
  border: none;
  margin-bottom: 5px;
}
h4 {
  text-align: left;
  color: white;
}
p {
  color: white;
  line-height: 25px;
}
.active-content {
  display: block;
}
li {
  position: relative;
  left: -15px;
  list-style-type: decimal;
}

/* ======= Pemerintahan Styling End ======= */

/* ======= Login Page Styling Start ======= */
.login {
  padding: 10rem 2rem 4rem 4rem;
  text-align: center;
}
.container {
  height: 600px;
  width: 500px;
  padding: 4rem 0;
  text-align: start;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 0.8rem;
  transition: 0.5s;
  border: 2px solid;
  border-radius: 15px;
}
.top {
  font-size: 2rem;
  padding: 2rem 0;
  display: flex;
  justify-content: space-between;
  gap: 50px;
}
.top i {
  cursor: pointer;
}
.devider {
  font-size: 2rem;
  border: 2px solid;
  background: white;
  height: 5px;
  width: 400px;
}

form {
  padding: 2rem;
  line-height: 40px;
  display: flex;
  flex-direction: column;

  font-size: 1rem;
}
form > input {
  width: 400px;
  border: none;
  border-radius: 5px;
}
form > button {
  border-radius: 5px;
}
form > button:hover {
  border: 1px solid white;
  background: black;
  color: white;
  transition: 0.5s;
}
.remember {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
}
.remember p {
  position: relative;
  top: 6px;
}
.bottom {
  text-align: center;
  line-height: 40px;
  display: flex;
  gap: 80px;
}
.bottom a {
  text-decoration: none;
  color: white;
}

/* ======= Login Page Styling End ======= */


/* ======= Kontak Styling Start ====== */

.web__kontak{
  padding: 2rem 4rem;
}
.kontak-top{
  text-align: center;
}
.aparatur{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 5rem 0;
}
.person__card{
  border: 2px solid white;
  border-radius: 5px;
  width: 400px;
  padding: 1rem 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.person__card img{
  width: 330px;
  height: 400px;
  padding-bottom: 1rem;
}
.card-head, 
.card-head h1{
  font-size: 25px;
  padding: 1rem 0;
}
.card-info{
  padding: 1rem 0;
  text-align: center;
}
.card-info button{
  display: flex;
  flex-direction: row;
  align-items: center;
  border: none;
  border-radius: 10px;
  background: #128c7e;
  transition: .3s;
}
.card-info button:hover {
  background: #128c7ebb;
}
.card-info button > a {
  text-decoration: none;
  color: black;
  font-size: 20px;
  position: relative;
  left: -5px;
}
.card-info button> a >img{
  width: 40px;
  height: 40px;
  position: relative;
  top: 7px;
}
.kontak__surat{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.kontak__form{
  border: 1px solid white;
  width: 900px;
}

.kontak__form-btn{
  padding: 2rem 0;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}
.kontak__form-btn button{
  width: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  text-transform: uppercase;
  border-radius: 10px;
  transition: .3s;
}
.kontak__form-btn button:hover{
  background: black;
  border: 1px solid white;
  color: white;
}

/* ======= Kontak Styling End ====== */


/* ======= Data Styling Start ======= */

.desa__data{
  padding: 2rem 4rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 4rem;
  font-size: 15px;
} 
.desa__data-dropdown{
padding: 0;
}
.dropdown{
position: relative;
padding: .50rem 0;
}

.dropdown button{
display: flex;
justify-content: space-between;
align-items: center;
width: 250px;
height: 40px;
font-size: 20px;
font-weight: 500;
color: white;
background: rgb(255, 81, 0);
border: none;
border-radius: 5px;
padding: 0 10px;
transition: .3s;
}
.dropdown button:hover{
background: rgb(186, 68, 14);
}
.dropdown button > span{
display: flex;
align-items: flex-end;
}
.dropdown__menu{
background: var(--color-bg);
width: 247px;
border: none;
border-bottom-left-radius: 5px;
border-bottom-right-radius: 5px;
display: flex;
flex-direction: column;
padding: .5rem 0 0 0;
}
.dropdown__menu li{
list-style: none;
color: var(--color-text);
font-size: 20px;
font-weight: 600;
line-height: 30px;
}
.dropdown__menu li > button{
background: transparent;
display: flex;
justify-content: flex-start;
transition: .3s;
position: relative;
left: -15px;
}
.dropdown__menu li > button:hover{
background: transparent;
color: rgb(0, 166, 255);
}
.menu{
position: relative;
right: 14px;
}
.dropdown__menu li > a > span{
font-size: 20px;
}

.table-content{
display: none;
}

.active-table{
display: block;
}



/* ======= Data Styling End ======= */





/* ======= Responsive Start ======= */

@media screen and (max-width: 1000px) {
  .web__navbar-links {
    display: none;
  }
  .nav__responsive {
    display: block;
  }
  .desa__data{
    display: flex;
    flex-direction: column;
    font-size: 11px;
  }
  .desa__data-dropdown button{
    font-size: 15px;
    width: 200px;
    height: 30px;
  }
  .dropdown__menu{
    width: 200px;
  }
  .table-content h1{
    font-size: 25px;
  }
}
@media screen and (max-width: 550px) {
  .web__navbar-links {
    display: none;
  }
  .nav__responsive {
    display: block;
  }
  .home__bar ul  > li{
    font-size: 14px;
  }
  .desa__data{
    display: flex;
    flex-direction: column;
    font-size: 9px;
  }
  .table-content h1{
    font-size: 25px;
  }
}
@media screen and (max-width: 300px) {
  .web__navbar-links {
    display: none;
  }
  .nav__responsive {
    display: block;
  }
}
.collapsible .content {
    padding: 6px;
    background-color: #eeeeee;
}
.collapsible .header {
    background-color: #dddddd;
    padding: 6px;
    cursor: pointer;
}

/* ======= Responsive End ======= */